@font-face {
  font-family: "Helvetica";
  font-style: normal;
  src: url("../fonts/helvetica_regular.otf");
  font-display: swap;
}
body {
  margin: 0;
  color: #000000;
  font-weight: 400;
  font-family: "Helvetica", "Arial", sans-serif;
  font-style: normal;
}
ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.header {
  box-sizing: border-box;
  max-width: 310px;
  margin: 0 auto;
  padding: 17px 16px;
}
@media (min-width: 1200px) {
  .header {
    max-width: 1142px;
    padding: 33px 16px;
  }
}
.header__favorites {
  width: 24px;
  height: 22px;
  margin-right: 16px;
  background-color: inherit;
  background-image: url("../images/heart.svg");
  background-repeat: no-repeat;
  background-size: 22px 22px;
  border: none;
  cursor: pointer;
}
@media (min-width: 1200px) {
  .header__favorites {
    width: 28px;
    height: 25px;
    background-size: 28px 25px;
  }
}
.header__profile {
  display: none;
}
@media (min-width: 1200px) {
  .header__profile {
    display: block;
    width: 22px;
    height: 25px;
    margin-right: 22px;
    margin-left: auto;
    background-image: url("../images/profile.svg");
    background-repeat: no-repeat;
    background-size: 22px 25px;
  }
}
.header__basket {
  display: block;
  width: 31px;
  height: 26px;
  background-image: url("../images/basket.svg");
  background-repeat: no-repeat;
  background-size: 26px 26px;
}
@media (min-width: 1200px) {
  .header__basket {
    height: 29px;
    background-size: 31px 29px;
  }
}
.header__wrapper-for-search {
  display: flex;
  margin-right: 42px;
}
.header__search-btn {
  width: 22px;
  height: 22px;
  background-color: inherit;
  background-image: url("../images/search.svg");
  background-repeat: no-repeat;
  background-size: 22px 22px;
  border: none;
  cursor: pointer;
}
@media (min-width: 1200px) {
  .header__search-btn {
    width: 25px;
    height: 25px;
    background-size: 25px 25px;
  }
}
.navigation__svg-logo {
  width: 74px;
  height: 24px;
  margin-right: 34px;
}
.header__list {
  display: none;
}
@media (min-width: 1200px) {
  .header__list {
    display: flex;
    margin-top: 70px;
  }
}
.header__item:not(:last-child):not(:nth-child(3)) {
  margin-right: 30px;
}
.header__item:nth-child(3) {
  margin-right: 288px;
}
.header__link {
  display: block;
  min-width: 117px;
  color: #9f9f9f;
  font-size: 18px;
  text-transform: uppercase;
  text-decoration: none;
}
.header__link:hover {
  color: #000000;
}
.header__search-input {
  display: none;
}
@media (min-width: 1200px) {
  .header__search-input {
    display: block;
    width: 250px;
    margin-left: 20px;
    color: #000000;
    font-size: 18px;
    border: none;
    border-bottom: 1px solid #9f9f9f;
  }
}
.header__search-input::placeholder {
  color: #9f9f9f;
  font-size: 18px;
}
.header__search-input:focus {
  color: #000000;
  outline: none;
}
.navigation {
  position: relative;
  display: flex;
  align-items: center;
}
@media (min-width: 1200px) {
  .navigation__link-logo {
    position: absolute;
    top: 65px;
    left: 471px;
  }
}
.navigation__svg-logo {
  width: 74px;
  height: 24px;
}
@media (min-width: 1200px) {
  .navigation__svg-logo {
    width: 160px;
    height: 50px;
  }
}
.navigation__toggle {
  position: relative;
  width: 28px;
  height: 20px;
  margin-right: 16px;
  background-color: inherit;
  border: none;
}
.navigation__toggle::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 10px 0 0 #000000, 0 20px 0 0 #000000;
  content: "";
}
@media (min-width: 1200px) {
  .navigation__toggle {
    display: none;
  }
}
.breadcrumbs {
  display: flex;
  margin-top: 22px;
}
@media (min-width: 1200px) {
  .breadcrumbs {
    margin-top: 15px;
  }
}
.breadcrumbs__item {
  margin-right: 26px;
}
.breadcrumbs__link {
  color: #9f9f9f;
  font-size: 18px;
  text-decoration: none;
}
.breadcrumbs__item--active .breadcrumbs__link {
  color: #000000;
}
.breadcrumbs__item:hover .breadcrumbs__link {
  color: #000000;
}
.breadcrumbs__item:not(:last-child) {
  position: relative;
}
.breadcrumbs__item:not(:last-child)::before {
  position: absolute;
  top: 0;
  right: -16px;
  content: "/";
}
.main {
  box-sizing: border-box;
  max-width: 310px;
  margin: 0 auto;
  padding: 0 16px 40px 16px;
}
@media (min-width: 1200px) {
  .main {
    max-width: 1110px;
    padding: 0 0 40px 0;
  }
}
@media (min-width: 1200px) {
  .main__wrapper {
    position: relative;
    width: 50%;
  }
}
.main__title {
  margin: 30px 0 10px 0;
  font-weight: 700;
  font-size: 30px;
}
@media (min-width: 1200px) {
  .main__title {
    margin: 50px 0 10px 0;
    font-size: 40px;
  }
}
.main__have-account {
  font-size: 16px;
}
.main__login-link {
  color: #9f9f9f;
  font-size: 16px;
}
.main__login-link:hover {
  color: #000000;
}
.form {
  display: flex;
  flex-wrap: wrap;
  margin-top: 40px;
}
.form__label {
  display: none;
}
.form__map {
  width: 287px;
  height: 200px;
  margin-bottom: 39px;
}
@media (min-width: 1200px) {
  .form__map {
    position: absolute;
    top: 68px;
    right: -555px;
    width: 555px;
    height: 700px;
  }
}
.form__input {
  position: relative;
  width: 287px;
  height: 38px;
  margin-bottom: 20px;
  color: #000000;
  font-size: 16px;
  border: none;
  border-bottom: 1px solid #9f9f9f;
}
@media (min-width: 1200px) {
  .form__input {
    width: 505px;
  }
}
.form__input:invalid {
  color: #d80909;
  border-bottom: 1px solid #d80909;
}
.form__input--invalid {
  border-bottom: 1px solid #d80909;
}
.form__focus {
  display: none;
}
.form__input::placeholder {
  color: #9f9f9f;
  font-size: 16px;
}
.form__input:focus {
  color: #000000;
  outline: none;
}
.form__input:focus::placeholder {
  color: #ffffff;
  outline: none;
}
.form__input:focus + .form__focus {
  position: absolute;
  top: -9px;
  display: block;
  color: #9f9f9f;
  font-size: 16px;
}
.form__element-wrapper {
  position: relative;
}
.form__invalid-text {
  display: none;
}
.form__input:invalid + .form__focus {
  display: none;
}
.form__input:invalid ~ .form__invalid-text {
  position: absolute;
  top: -9px;
  display: block;
  color: #d80909;
  font-size: 12px;
}
.form__input:valid ~ .form__invalid-text {
  display: none;
}
.form__input:required ~ .form__invalid-text {
  display: none;
}
@media (min-width: 1200px) {
  .form__input--name {
    width: 235px;
    margin-right: 35px;
  }
}
@media (min-width: 1200px) {
  .form__input--phone {
    width: 235px;
  }
}
.form__buy-btn {
  width: 288px;
  height: 50px;
  font-weight: 700;
  font-size: 20px;
  background-color: inherit;
  border: 1px solid #000000;
  cursor: pointer;
}
@media (min-width: 1200px) {
  .form__buy-btn {
    width: 505px;
    height: 70px;
  }
}
.form__buy-btn:hover {
  color: #ffffff;
  background-color: #000000;
}
.form__option-group {
  border: 1px solid #9f9f9f;
}
.form__option {
  min-height: 101px;
  color: #9f9f9f;
}
.products__item {
  display: grid;
  grid-template-rows: 50% 50%;
  grid-template-columns: 80px 80px auto;
  margin-bottom: 18px;
  padding-bottom: 14px;
  column-gap: 12px;
  border-bottom: 1px solid #dedede;
}
@media (min-width: 1200px) {
  .products__item {
    grid-template-rows: auto auto auto;
    grid-template-columns: 150px 150px auto;
    width: 506px;
    margin-bottom: 32px;
  }
}
.products__title {
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 26px;
}
@media (min-width: 1200px) {
  .products__title {
    margin-top: 56px;
    font-size: 28px;
  }
}
.products__picture {
  grid-row: 1/-1;
  grid-column: 1/2;
}
.products__item-image {
  width: 80px;
  height: 80px;
}
@media (min-width: 1200px) {
  .products__item-image {
    width: 150px;
    height: 150px;
  }
}
.products__item-description {
  display: none;
}
@media (min-width: 1200px) {
  .products__item-description {
    display: block;
    grid-row: 2/3;
    grid-column: 2/3;
    color: #9f9f9f;
    font-size: 16px;
  }
}
.products__item-name {
  grid-row: 1/2;
  grid-column: 2/3;
  font-weight: 700;
  font-size: 16px;
}
@media (min-width: 1200px) {
  .products__item-name {
    font-size: 20px;
  }
}
.products__item-price {
  grid-row: 1/2;
  grid-column: 3/4;
  font-weight: 700;
  font-size: 16px;
  text-align: right;
}
@media (min-width: 1200px) {
  .products__item-price {
    font-size: 20px;
  }
}
.products__item__wrapper-for-counter {
  display: flex;
  grid-row: 2/3;
  grid-column: 2/3;
  justify-content: space-between;
  box-sizing: border-box;
  width: 81px;
  height: 34px;
  padding: 5px;
  border: 1px solid #000000;
}
@media (min-width: 1200px) {
  .products__item__wrapper-for-counter {
    grid-row: 3/4;
    width: 114px;
    height: 40px;
  }
}
.products__item-button {
  position: relative;
  background-color: inherit;
  border: none;
}
.products__item-button:hover {
  cursor: pointer;
}
.products__item-button--minus::before,
.products__item-button--plus::before {
  position: absolute;
  width: 12px;
  height: 2px;
  background-color: #000000;
  content: "";
}
@media (min-width: 1200px) {
  .products__item-button--minus::before,
  .products__item-button--plus::before {
    width: 16px;
  }
}
.products__item-button--plus::before {
  right: 5px;
}
@media (min-width: 1200px) {
  .products__item-button--plus::before {
    right: 10px;
  }
}
.products__item-button--plus::after {
  position: absolute;
  right: 5px;
  width: 12px;
  height: 2px;
  background-color: #000000;
  transform: rotate(90deg);
  content: "";
}
@media (min-width: 1200px) {
  .products__item-button--plus::after {
    right: 10px;
    width: 16px;
  }
}
.products__item-button-delete {
  grid-row: 2/3;
  grid-column: 3/4;
  color: #9f9f9f;
  font-size: 12px;
  text-align: right;
  text-decoration: underline;
  background-color: inherit;
  border: none;
}
@media (min-width: 1200px) {
  .products__item-button-delete {
    grid-row: 3/4;
    font-size: 14px;
  }
}
.products__item-button-delete:hover {
  color: #000000;
  cursor: pointer;
}
.products__item-input-counter {
  width: 20px;
  margin-right: 9px;
  margin-left: 9px;
  font-weight: 700;
  font-size: 16px;
  text-align: center;
  border: none;
}
@media (min-width: 1200px) {
  .products__item-input-counter {
    width: 29px;
    margin-left: 21px;
    font-size: 20px;
  }
}
.products__item-input-counter:focus {
  outline: none;
}
.products__total {
  font-weight: 700;
  font-size: 26px;
}
@media (min-width: 1200px) {
  .products__total {
    font-size: 40px;
  }
}
.products__buy-btn:hover {
  color: #ffffff;
  background-color: #000000;
}
.products__total-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 31px;
}
@media (min-width: 1200px) {
  .products__total-wrapper {
    position: absolute;
    top: 634px;
    left: 555px;
    box-sizing: border-box;
    width: 555px;
    height: 136px;
    padding: 43px 35px 0 35px;
    color: #ffffff;
    background-color: rgba(0, 0, 0, 0.8);
  }
}
.selectCustom {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
}
@media (hover: hover) {
  .selectCustom {
    display: block;
  }
  .selectNative:focus + .selectCustom {
    display: none;
  }
}
.select {
  position: relative;
  margin-bottom: 20px;
}
.selectLabel {
  display: block;
  font-weight: bold;
}
.selectWrapper {
  position: relative;
}
.selectNative,
.selectCustom-trigger {
  position: relative;
  box-sizing: border-box;
  width: 287px;
  height: 38px;
  padding-top: 8px;
  color: #9f9f9f;
  font-size: 16px;
  background-color: #ffffff;
  border: none;
  border-bottom: 1px solid #9f9f9f;
}
@media (min-width: 1200px) {
  .selectNative,
  .selectCustom-trigger {
    width: 505px;
  }
}
.selectNative {
  background-image: url("../images/arrow.svg");
  background-repeat: no-repeat;
  background-position-x: 100%;
  background-position-y: 0.8rem;
  -webkit-appearance: none;
  -moz-appearance: none;
}
.selectCustom-trigger {
  position: relative;
  width: 287px;
  height: 38px;
  color: #9f9f9f;
  font-size: 16px;
  border: none;
  border-bottom: 1px solid #9f9f9f;
}
@media (min-width: 1200px) {
  .selectCustom-trigger {
    width: 505px;
  }
}
.selectCustom-trigger::after {
  position: absolute;
  top: 9px;
  right: 4px;
  width: 16px;
  height: 9px;
  line-height: 3.8rem;
  background-image: url("../images/arrow.svg");
  background-repeat: no-repeat;
  background-position-x: 100%;
  content: "";
}
.selectCustom-options {
  position: absolute;
  top: 40px;
  left: 0;
  z-index: 1;
  display: none;
  width: 100%;
  color: #9f9f9f;
  background-color: #ffffff;
  border: 1px solid #9f9f9f;
  border-top: none;
  box-shadow: 0 0 4px #e9e1f8;
}
.selectCustom.isActive .selectCustom-options {
  display: block;
}
.selectCustom-option {
  position: relative;
  padding: 20px 0 0 20px;
}
.selectCustom-option:last-child {
  padding-bottom: 20px;
}
.selectCustom-option.isActive {
  color: #000000;
}
.selectCustom-trigger--active {
  position: relative;
  color: #000000;
}
.selectCustom-trigger--active::before {
  position: absolute;
  top: -9px;
  color: #9f9f9f;
  font-size: 12px;
  content: "Тип упаковки";
}
.selectCustom-trigger--invalid {
  border-color: #d80909;
}
